// Default typography styling.
//

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 500;
}

h1 {
  @include fluid-type(320px, 1200px, 38px, 40px);
}

h2 {
  @include fluid-type(320px, 1200px, 30px, 32px);
}

h3 {
  @include fluid-type(320px, 1200px, 26px, 28px);
}

h4 {
  @include fluid-type(320px, 1200px, 22px, 24px);
}

h5 {
  @include fluid-type(320px, 1200px, 18px, 20px);
}

h6 {
  @include fluid-type(320px, 1200px, 14px, 16px);
}

p {
  @include fluid-type(320px, 1200px, 16px, 18px);
}
